 @import '../../Global.scss';


.contact{
  
  background-color: rgb(255, 255, 255);
  border-radius: 20px;
    .container{        
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content:center ;
        @include mobile{
            flex-direction: column;
        }
         
       .left{
          width: 50%;
          height: 100%;
          @include mobile{
            width: 100%;
            height: 90%;  
          }
          img{
            width: 600px;
            height: 500px;
            @include mobile{
                width: 380px;
                height: 200px;
            }
          }
        }
        .right{

            width: 50%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            @include mobile{
                width: 200%;
            }
          
          .title{
             margin-bottom: 20px;
             @include mobile{
                font-size: 28px;
             }
          }

          form{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
          .email{
             width: 400px;
             height: 30px;
             margin-bottom: 20px;
             @include mobile{
                width: 250px;
                height: 25px;
             }
          }

          textarea{
           width: 400px;
           height: 250px; 
           margin-bottom: 20px;
             @include mobile{
                width: 250px;
                height: 155px;
             }
             
          }
          span{
              color: rgb(0, 255, 0);
              font-size: 20px;
          } 
          button{
            width: 80px;
            height: 30px;
            border-radius: 10px;
            background-color: crimson;
          }
           }  
        }  
    }

}